.footer {
    background-color: #222;
    color: #fff;
    padding: 2rem 1rem;
    font-size: 0.9rem;
  
    &_content {
      display: flex;
      flex-wrap: wrap;
      justify-content: space-between;
      gap: 2rem;
      max-width: 1200px;
      margin: 0 auto;
    }
  
    &_column {
      flex: 1 1 200px;
  
      h3 {
        color: #bc922e;
        margin-bottom: 0.5rem;
      }
  
      ul {
        list-style: none;
        padding: 0;
  
        li {
          margin-bottom: 0.3rem;
  
          a {
            color: #fff;
            text-decoration: none;
  
            &:hover {
              color: #bc922e;
            }
          }
        }
      }
  
      p, a {
        margin: 0.2rem 0;
        color: #ccc;
  
        &:hover {
          color: #bc922e;
        }
      }
    }
  
    &_logo {
      max-height: 60px;
      margin-top: 1rem;
    }
  
    &_bottom {
      border-top: 1px solid #444;
      margin-top: 2rem;
      padding-top: 1rem;
      text-align: center;
  
      a {
        color: #ccc;
        text-decoration: none;
  
        &:hover {
          color: #bc922e;
        }
      }
    }
  
    @media (max-width: 768px) {
      &_content {
        flex-direction: column;
        align-items: center;
        text-align: center;
      }
  
      &_column {
        flex: none;
      }
    }
  }
  